//
export const getRangeRandom = (low, high) => {
  return Math.floor(Math.random() * (high - low) + low);
}

//0-30
export const getDegRandom = () => {
  return ((Math.random() > 0.5
    ? '+'
    : '-') + Math.floor(Math.random() * 30));
}


